.prompt {
  margin-right: .5rem;
}

.commandLine {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: .25rem;
}

.resultArea {
  display: -ms-flexbox;
  display: flex;
  margin-bottom: .25rem;
}

.resultArea a {
  margin-right: .5rem;
}

input {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  width: 100%;
}

@-webkit-keyframes blink-cursor {
  0%,
  50% {
    background-color: #000;
  }

  50%,
  100% {
    background-color: #fff;
  }
}

@keyframes blink-cursor {
  0%,
  50% {
    background-color: #000;
  }

  50%,
  100% {
    background-color: #fff;
  }
}



